:root {
  --main-font: "DM Sans", sans-serif;
  --black-color: #000;
  --light-black-color: #111;
  --white-color: #fff;
  --blue-color: #4f2ee8;
  --dark-grey-color: #202024;
  --light-grey-color: #f6f6f6;
  --grey-text-color: #b4afaf;
  --yellow-color: #eac645;
  --transition-duration: 250ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  font-weight: inherit;
  background: none;
  border: none;
  outline-color: rgba(0, 0, 0, 0);
  font-family: inherit;
}

a {
  text-decoration: none;
}

body {
  font-size: var(--main-font);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.12;
}

.aside-wrapper {
  padding-top: 40px;
  padding-bottom: 40px;
}

@media only screen and (min-width: 768px) {
  .aside-wrapper {
    gap: 32px;
    padding-bottom: 100px;
    display: flex;
  }
}

@media only screen and (min-width: 1440px) {
  .aside-wrapper {
    flex-direction: column;
    gap: 86px;
    padding-bottom: 0;
  }
}

@media only screen and (min-width: 1440px) {
  .main-content {
    flex-grow: 1;
  }
}

.container {
  max-width: 375px;
  outline: 1px solid red;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    gap: 40px;
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
  }
}















